/* modules */

.popup {
  margin: 20px auto;
  max-width: 300px;
  background: $color-orange;
  border: 2px solid $color-orange;
  box-shadow: 0 0 0 3px $color-dark inset;
  padding: 20px 27px 20px 27px;
  color: $color-white;
  position: relative;
  &--senks {
    background: {
      position: center;
      repeat: no-repeat;
      image: url('data:image/png;base64,'); }
    padding-top: 80px;
    padding-bottom: 40px; }
  &__title {
    text-transform: uppercase;
    text-align: center;
    line-height: 1.2;
    font-family: $font-opensans-cb;
    font-size: rem(20);
    margin-bottom: 15px; }
  &__info {
    text-align: center;
    font-size: rem(14);
    color: #ffd9cd;
    margin-bottom: 20px;
    font-family: $font-opensans-cb; }

  &__separator {
    color: $color-white;
    display: table;
    width: 100%;
    margin-bottom: 10px;
    &>* {
      display: table-cell;
      vertical-align: middle; }
    &::before, &::after {
      content: '';
      display: table-cell;
      width: 50%;
      background-image: linear-gradient(#fba084, #fba084);
      background-size: 100% 1px;
      background-repeat: no-repeat;
      background-position: center; }
    &>span {
      white-space: nowrap;
      padding: 0 10px;
      font-family: $font-opensans-cb;
      font-size: rem(15); } }
  &__bottom {
    background: $color-dark;
    text-align: center;
    position: relative;
    margin: 15px -27px -20px -27px;
    padding: 20px 0;
    &::before {
      content: '';
      position: absolute;
      top: 0;
      left: 50%;
      margin-left: -8px;
      width: 0;
      height: 0;
      border-left: 8px solid transparent;
      border-right: 8px solid transparent;
      border-top: 8px solid $color-orange; }
    .btn {
      padding-left: 10px;
      padding-right: 10px;
      min-width: 180px; } } }

.mfp-close-btn-in .mfp-close {
  color: $color-white;
  opacity: 1; }

.comment {
  &__head {
    height: 25px;
    margin: 0 -15px 27px -15px;
    background: $color-dark;
    color: $color-white;
    font-size: rem(14);
    text-transform: uppercase;
    font-family: $font-opensans-cb;
    padding: 0 15px;
    line-height: 25px;
    &--left {
      float: left; }
    &--right {
      float: right;
      white-space: nowrap;
      margin-right: -15px; }
    @include clearfix; }
  &__block {
    position: relative;
    margin-bottom: 30px;
    height: 224px;
    color: #535353;
    font-size: rem(13);
    cursor: pointer;
    &-wrapper {
      height: 224px;
      overflow: hidden;
      padding: 17px;
      border: 2px solid #f2f3f5;
      position: absolute;
      left: 0;
      right: 0;
      top: 0;
      // transition: background 0.35s
      @include clearfix; }
    &:hover {
      .comment__block-wrapper {
        color: #9e9e9e;
        background: $color-dark;
        border-color: $color-dark;
        height: auto;
        z-index: 10;
        box-shadow: 6px 0 20px rgba($color-dark,.5);
        overflow: inherit;
        table {
          color: #9e9e9e; } }
      .comment__text {
        height: auto;
        overflow: inherit; }
      .comment__name {
        color: $color-white; } } }
  &__name {
    font-size: rem(20);
    font-family: $font-roboto-bold;
    line-height: 1.2;
    margin-bottom: 5px;
    color: #232323;
    // transition: color 0.35s
    small {
      font-family: $font-roboto;
      font-size: rem(14);
      display: block; } }
  &__text {
    height: 137px;
    overflow: hidden;
    table {
      margin-bottom: 15px;
      font-family: $font-roboto-bold;
      color: #7d7c7c;
      td {
        i {
          color: #c2c7d3; }
        &:first-child {
          text-align: center;
          width: 25px; }
        a {
          text-decoration: none;
          &:hover {
            text-decoration: underline; } } } } }
  &__select {
    display: inline-block;
    vertical-align: -7px;
    .nice-select {
      // z-index: 20
      height: 25px;
      line-height: 23px;
      border-color: transparent;
      padding-left: 10px;
      padding-right: 25px;
      background: #4b4b4b;
      border-radius: 0;
      margin-left: 10px;
      .list {
        border: 2px solid $color-dark;
        border-radius: 0;
        box-shadow: none;
        font-size: 13px;
        margin-left: -5px;
        color: $color-orange; }
      .current {
        font-weight: 600; }
      .option {
        min-height: 35px;
        line-height: 35px;
        padding-left: 10px;
        padding-right: 20px;
        &.selected {
          font-weight: 600;
          color: $color-orange;
 } } } }          // background-color: $color-s-lightbrown
  &__row {
    color: #232323;
    font-size: rem(13);
    margin-bottom: 23px;
    border-bottom: 1px solid #e7e7e7;
    padding-bottom: 23px;
    &:last-child {
      border-bottom: 0; }
    p {
      line-height: 18px;
      &:last-child {
        margin-bottom: 0; } }
    &-title {
      font-size: rem(15);
      color: #232323;
      font-family: $font-opensans-cb;
      text-transform: uppercase;
      float: left;
      &>span {
        display: inline-block;
        margin-left: 10px;
        color: $color-orange;
        text-transform: uppercase;
        i {
          font-size: rem(13); }
        small {
          font-size: rem(11);
          color: #ababab;
          display: inline-block;
          margin: 0 5px;
          text-transform: none; } } }
    &-btn {
      float: right;
      text-transform: uppercase;
      font-size: rem(15);
      text-decoration: none;
      color: $color-orange;
      font-family: $font-opensans-cb;
      &.disabled {
        cursor: default;
        color: #aaaaaa;
        &:hover {
          text-decoration: none; } }
      &:hover {
        text-decoration: underline; }
      i {
        margin-right: 10px;
        vertical-align: -3px; } }
    &-block {
      padding-left: 40px;
      padding-top: 10px;
      position: relative;
      background: url('data:image/png;base64,') 0 0 no-repeat; }
    &-head {
      font-size: rem(16);
      font-family: $font-opensans-cb;
      color: #232323;
      span {
        color: $color-orange; }
      margin-bottom: 20px; } } }

.search-result {
  &__head {
    height: 25px;
    margin: 0 -15px 27px -15px;
    background: $color-dark;
    color: $color-white;
    font-size: rem(14);
    text-transform: uppercase;
    font-family: $font-opensans-cb;
    padding: 0 15px;
    line-height: 25px;
    &--left {
      float: left; }
    &--right {
      float: right;
      white-space: nowrap;
      margin-right: -15px; }
    @include clearfix; }
  &__select {
    display: inline-block;
    vertical-align: -7px;
    .nice-select {
      // z-index: 20
      height: 25px;
      line-height: 23px;
      border-color: transparent;
      padding-left: 10px;
      padding-right: 25px;
      background: #4b4b4b;
      border-radius: 0;
      margin-left: 10px;
      .list {
        border: 2px solid $color-dark;
        border-radius: 0;
        box-shadow: none;
        font-size: 13px;
        margin-left: -5px;
        color: $color-orange; }
      .current {
        font-weight: 600; }
      .option {
        min-height: 35px;
        line-height: 35px;
        padding-left: 10px;
        padding-right: 20px;
        &.selected {
          font-weight: 600;
          color: $color-orange;
 } } } }          // background-color: $color-s-lightbrown
  &__not-result {
    height: 462px;
    display: flex;
    justify-content: center;
    align-items: center;
    background: #f2f3f5 url('data:image/png;base64,') center no-repeat; }
  &__phone {
    text-align: center;
    margin-bottom: 27px;
    font-family: $font-roboto-bold;
    strong {
      display: block;
      line-height: 1;
      font-size: rem(30);
      color: $color-orange;
      letter-spacing: 5px;
      margin: 10px 0; }
    span {
      text-transform: uppercase;
      color: #232323;
      font-size: rem(18);
      display: inline-block;
      letter-spacing: 2px; }
    small {
      font-size: rem(14); } }
  &__list {
    display: table;
    width: 100%; }
  &__row {
    color: #232323;
    font-size: rem(13);
    font-family: $font-roboto;
    display: table-row;
    &>div {
      display: table-cell;
      vertical-align: middle;
      position: relative;
      padding: 10px 15px;
      width: calc(100% / 6);
      border-bottom: 1px solid #e7e7e7; }
    &:nth-child(odd) {
      &>div {
        background: #fafafb; } }
    .btn {
      position: absolute;
      right: 5px;
      top: 4px;
      display: none;
      box-shadow: none;
      &:hover {
        color: $color-orange;
        background: $color-white; } }
    &-link {
      text-decoration: none; }
    &:hover {
      &>div {
        background: #232323;
        color: #9e9e9e;
        &.user {
          color: $color-white; } }
      .btn {
        display: block; } } } }

.people {
  margin-bottom: 25px;
  border: 2px solid #f2f3f5;
  padding: 17px;
  @include clearfix;
  &__wrapper {
    display: table;
    width: 100%;
    &>div {
      display: table-cell;
      vertical-align: middle; }
    .btn {
      float: none;
      min-width: inherit;
      padding: 0;
      height: auto;
      line-height: inherit;
      margin: 0;
      width: auto;
      display: inline-block;
      &:hover {
        color: $color-orange;
        text-decoration: underline; } } }
  &__name {
    font-size: 1.25rem;
    font-family: "RobotoBold";
    line-height: 1.2;
    margin-bottom: 5px;
    color: #232323;
    small {
      font-family: "RobotoRegular";
      font-size: 0.875rem;
      display: block; } }
  &__table {
    font-family: "RobotoBold";
    color: #7d7c7c;
    float: right;
    min-width: 150px;
    td {
      &:first-child {
        text-align: center;
        width: 25px; }
      i {
        color: #c2c7d3; } }
    a {
      text-decoration: none;
      &:hover {
        text-decoration: underline; } } } }
